body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.form {
  font-family: monospace;
}
.form__title {
  color: #9351b4;
}
.form__text{
  font-size: 16px;
}
.button {
  color: white;
  font-size: 1.3rem;
  font-weight: bold;
  width: 100%;
  padding: 0.8rem;
  margin-bottom: 1rem;
  background: #9351b4;
  border: 1px solid #9351b4;
  border-radius: 5px;
}

.button--disabled {
  color: gray;
  background: lightgrey;
  border-color: lightgrey;
}
.input {
  font-size: 1rem;
  box-sizing: border-box;
  display: block;
  width: 100%;
  padding: 0.8rem;
  margin: 0 0 1rem;
  border: 1px solid black;
  border-radius: 5px;
}
.input:focus {
  outline: none;
  border-color: #9351b4;
  box-shadow: 0 0 10px #9351b4;
}

.input__erro {
  color: red;
}
.label {
  font-weight: bold;
  font-family: monospace;
  font-size: 18px;
  display: block;
  margin-bottom: 0.3rem;
}
.link {
  cursor: pointer;
  color: #9351b4;
  text-align: center;
  text-decoration: underline;
  display: block;
}
.container {
    padding: 0.75rem 1rem 0;
}

@media (min-width: 768px) {
    .container {
        padding: 1.5rem 20% 0;
    }
}

@media (min-width: 992px) {
    .container {
        padding: 2.25rem 30% 0;
    }
}
